<template>
    <div class="index-wrap">
      <div class="want-play-wrap">
         <span class="title">我也要玩</span>
         <input type="file" accept="image/*"  class="btn"  @click="playCamera"  multiple="multiple" capture="camera">
      </div>
    </div>
</template>

<script>
    export default {
      data(){
        return {

        }
      },
      methods:{
        playCamera(){

        }
      },
      created(){
        window.navigator.getUserMedia = navigator.getUserMedia || navigator.webKitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia;

      }

    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
  .index-wrap{
    text-align: center;
  }
  .want-play-wrap{
    position: relative;
    margin: 500px auto 0;
    width: 100px;
    height: 30px;
    line-height: 30px;
    background: #ff8930;
    color: #fff;
    .title{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
    .btn{
      opacity: 0;
    }
  }
</style>



